import React from "react";
import styles from "./ImageContent.module.scss";

import { imageObj } from "../../imageImports";
import { Carousel } from "react-responsive-carousel";
import clsx from "clsx";

const ImageContent = (props) => {
  if (props.type === "carousel") {
    return (
      <div
        className={
          props.hidden
            ? "inactive"
            : props.index === 3
            ? styles[`container`] + " " + styles["blur"]
            : styles["container"]
        }
      >
        <Carousel
          renderArrowPrev={(onClickHandler) => {
            return (
              <button
                onClick={onClickHandler}
                className={styles["control-arrow"] + " " + styles["left"]}
                type="button"
              ></button>
            );
          }}
          renderArrowNext={(onClickHandler) => {
            return (
              <button
                onClick={onClickHandler}
                className={styles["control-arrow"] + " " + styles["right"]}
                type="button"
              ></button>
            );
          }}
          autoPlay={false}
          className={styles[`carousel-container`]}
        >
          {imageObj.floorPlans.map((img) => {
            return (
              <div className={styles[`carousel-image-container`]}>
                <img src={img} alt="" />;
              </div>
            );
          })}
        </Carousel>
      </div>
    );
  }
  return (
    <div
      className={
        props.hidden
          ? "inactive"
          : props.index === 3
          ? styles[`container`] + " " + styles["blur"]
          : styles["container"]
      }
    >
      {!props.type ? (
        <div
          onClick={() => {
            if (props.index === 3) {
              props.handleShowModal("video");
            } else {
              return;
            }
          }}
        >
          {props.index === 3 ? (
            <img src={imageObj.misc[5]} className={styles[`play-button`]}></img>
          ) : null}
          <div className={styles[`image-container`]}>
            <img
              src={imageObj.bgPhotos[props.index]}
              alt=""
              className={styles[`main-image`]}
            />
          </div>
        </div>
      ) : (
        <div className={styles[`gallery-container`]}>
          <ul
            className={
              styles[`grid-container`] +
              " " +
              clsx({
                [styles["grid-left"]]: props.page === "left",
                [styles["grid-right"]]: props.page !== "left",
              })
            }
          >
            {imageObj.bts
              .slice(
                props.page === "left" ? 0 : 6,
                props.page === "left" ? 7 : 14
              )
              .map((img, i) => {
                return (
                  <li
                    className={clsx({
                      [styles["landscape"]]: i === 0 || i === 3 || i === 5,
                    })}
                  >
                    <img src={img} alt="" />
                  </li>
                );
              })}
            <iframe
              height="100%"
              className={styles[`video-modal`]}
              width="100%"
              src="https://www.youtube.com/embed/nTS10ZQM5Ms"
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          </ul>
        </div>
      )}
    </div>
  );
};

export default ImageContent;
