import React, { useRef } from "react";
import styles from "./Hamburger.module.scss";
import { imageObj } from "../../imageImports";
const Logo = imageObj.socials[4];
console.log(Logo,234234)
const Hamburger = (props) => {
  const scrollRef = useRef();
  const calculateScrollHeights = (windowHeight) => {
    scrollRef.current = [
      {
        left: 0,
        right: windowHeight * -5,
      },
      {
        left: windowHeight,
        right: windowHeight * -4,
      },
      {
        left: windowHeight * 2,
        right: windowHeight * -3,
      },
      {
        left: windowHeight * 3,
        right: windowHeight * -2,
      },
      {
        left: windowHeight * 4,
        right: windowHeight * -1,
      },
      {
        left: windowHeight * 5,
        right: windowHeight * 0,
      },
    ];
  };
  const inputRef = useRef();
  const navButtons = [
    "home",
    "malibu life",
    "discover",
    "equestrian",
    "video",
    "pictures",
  ];
  const handleNavScroll = (index) => {
    if (window.innerWidth <= 650) {
      window.location.href = "#" + navButtons[index];
    } else {
      calculateScrollHeights(window.innerHeight);
      props.scrollClick({ ...scrollRef.current[index], index });

      const { leftPage, rightPage } = props;

      leftPage.current.style.transform = `translateY(${-scrollRef.current[index]
        .left}px)`;
      rightPage.current.style.transform = `translateY(${scrollRef.current[index].right}px)`;
    }
    inputRef.current.checked = false;
  };

  return (
    <div>
      <input
        ref={inputRef}
        type="checkbox"
        id="burger-trigger"
        className={styles["burger-input"]}
      />
      <label htmlFor="burger-trigger" className={styles["burger-label"]}>
        <span className={styles["main-trigger-icon-container"]}>
          <i className={styles["main-trigger-icon"]}></i>
        </span>
      </label>{" "}
      <div className={styles["side-menu-container"]}>
        <ul
          name="list-container"
          className={styles["side-menu-item-container"]}
        >
          {navButtons.map((ele, i) => {
            return (
              <li
                key={i}
                onClick={() => {
                  handleNavScroll(i);
                }}
              >
                {ele}
              </li>
            );
          })}
        </ul>
        <div className={styles["sidebar-logo-container"]}>
          <a href="https://www.apeldesign.com/">
            <Logo className={styles["sidebar-logo"]} alt="" />
          </a>
        </div>
      </div>{" "}
      <div data-id="header-mask" className={styles["header-mask"]}></div>
    </div>
  );
};

export default Hamburger;
